// src/pages/MyPage/VodPlayer.jsx
import React, { useEffect, useRef } from "react";
import { useParams, useSearchParams } from "react-router-dom";
import {
  getVodInfo,
  updateAccessTime,
  updateCurrentTime,
} from "../../services/mypageService"; //  

function VodPlayer() {
  const { vodId } = useParams();
  const [searchParams] = useSearchParams();
  const orderCode = searchParams.get("order_code");

  const videoRef = useRef(null);
  const currentTimeRef = useRef(0);
  const currentState = useRef("N"); //  
  const initialTime = useRef(0); //  

  useEffect(() => {
    async function fetchVod() {
      try {
        const data = await getVodInfo(vodId, orderCode);

        if (
          data.vodHistory &&
          data.vodHistory.currentTime > 0 &&
          data.vodHistory.currentTime < data.vod.vodTime * 60
        ) {
          currentState.current = "Y";
          initialTime.current = data.vodHistory.currentTime;
        }

        setTimeout(() => handleInitialPlay(), 1000); // 1   
      } catch (error) {
        console.error("VOD   :", error);
      }
    }

    fetchVod();
  }, [vodId, orderCode]);

  function handleInitialPlay() {
    const video = videoRef.current;
    if (!video) return;

    if (currentState.current === "Y") {
      if (
        window.confirm("  .\n\n ?")
      ) {
        video.currentTime = initialTime.current;
      }
    }
    video.play();
    video.volume = 0.25;
  }

  // 30  
  useEffect(() => {
    const interval = setInterval(() => {
      updateAccessTime(vodId, orderCode);
    }, 30000);

    return () => clearInterval(interval);
  }, [vodId, orderCode]);

  // 10 
  useEffect(() => {
    const interval = setInterval(() => {
      const video = videoRef.current;
      if (video) {
        video.pause();
        if (window.confirm("   ? (10 )")) {
          video.play();
        }
      }
    }, 600000); // 10

    return () => clearInterval(interval);
  }, []);

  //       
  useEffect(() => {
    const handleBeforeUnload = (e) => {
      const video = videoRef.current;
      if (video) {
        currentTimeRef.current = video.currentTime;
        updateCurrentTime(vodId, orderCode, currentTimeRef.current);
      }
    };

    window.addEventListener("beforeunload", handleBeforeUnload);
    return () => window.removeEventListener("beforeunload", handleBeforeUnload);
  }, [vodId, orderCode]);

  return (
    <div style={{ backgroundColor: "#000", height: "100vh" }}>
      <video
        ref={videoRef}
        controls
        autoPlay
        width="100%"
        height="100%"
        style={{ objectFit: "contain" }}
        controlsList="nodownload"
      >
        {/* src getVodInfo API   */}
        <source src="/media/sample.mp4" type="video/mp4" />
             .
      </video>
    </div>
  );
}

export default VodPlayer;
