// src/pages/Member/LoginPage.jsx
import { useState } from "react";
import { useNavigate, useLocation, Link } from "react-router-dom";
import Breadcrumbs from "../../components/Breadcrumbs";
import { useAuth } from "../../context/AuthContext"; // AuthContext   .
import axios from "axios";

function LoginPage() {
  const [memberId, setMemberId] = useState("");
  const [passwd, setPasswd] = useState("");
  const navigate = useNavigate();
  const location = useLocation();
  const { login, isLoggedIn } = useAuth();

  //     
  if (isLoggedIn) {
    navigate("/");
    return null;
  }

  //  
  const handleLogin = async () => {
    if (!memberId.trim()) {
      alert("  .");
      return;
    }
    if (!passwd.trim()) {
      alert("  .");
      return;
    }

    try {
      const response = await axios.post("/api/member/login", {
        memberId,
        passwd,
      });

      if (response.data.success) {
        // AuthContext   
        login(response.data.token);

        //      
        const from = location.state?.from?.pathname || "/";
        navigate(from);
      } else {
        alert(response.data.message || " .");
      }
    } catch (error) {
      console.error(" :", error);
      alert("    .");
    }
  };

  //    
  const handleKeyDown = (e) => {
    if (e.key === "Enter") {
      handleLogin();
    }
  };

  return (
    <>
      {/* ======= Breadcrumbs ======= */}
      <Breadcrumbs
        title=" "
        description="Est dolorum ut non facere possimus quibusdam eligendi voluptatem. 
        Quia id aut similique quia voluptas sit quaerat debitis. 
        Rerum omnis ipsam aperiam consequatur laboriosam nemo harum praesentium."
      />
      {/* ======= End Breadcrumbs ======= */}

      <section className="section section-shaped section-lg">
        <div className="container pt-lg-7">
          <div className="row justify-content-center">
            <div className="col-lg-5">
              <div className="card bg-secondary shadow border-0">
                <div className="card-header bg-white pb-5 text-center">
                  <h2> </h2>
                </div>
                <div
                  className="card-body px-lg-5 py-lg-5"
                  style={{ background: "#c1c1c1" }}
                >
                  <div className="form-group mb-3">
                    <input
                      type="text"
                      className="form-control"
                      placeholder=""
                      value={memberId}
                      onChange={(e) => setMemberId(e.target.value)}
                      onKeyDown={handleKeyDown}
                    />
                  </div>
                  <div className="form-group focused">
                    <input
                      type="password"
                      className="form-control"
                      placeholder=""
                      value={passwd}
                      onChange={(e) => setPasswd(e.target.value)}
                      onKeyDown={handleKeyDown}
                    />
                  </div>
                  <div className="text-center">
                    <button
                      type="button"
                      className="btn btn-primary my-4"
                      style={{ background: "#5fcf80", borderColor: "#5fcf80" }}
                      onClick={handleLogin}
                    >
                      
                    </button>
                  </div>
                  <div className="row mt-3">
                    <div className="col-6">
                      <Link to="../register">
                        <small></small>
                      </Link>
                    </div>
                    <div className="col-6 text-end">
                      <Link to="../find-idpw">
                        <small>/ </small>
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default LoginPage;
